<template>
    <div>
        <div id="element-to-convert" class="px-3">
            <center>
                <div id="html2pdf" ref="document" class="mx-4 my-2">
                    <div class="img-container mb-3">
                        <img src="../assets/tfs-logo.jpg" width="80">
                    </div>
                    <div class="row">
                        <div class="col-md-6 offset-md-3">
                            <input @keyup.enter="toggleTittleEdit" v-if="titleEdit" type="text" class="form-control mb-4" v-model="title" placeholder="Enter title ...">
                            <h4 v-if="titleEdit===false" class="mb-4">{{ title }}<i class="fa-solid fa-pen-to-square ms-2 text-primary" @click="toggleTittleEdit"></i></h4>
                        </div>
                    </div>
                    <table>
                        <thead >
                            <tr class="bg-light">
                                <th scope="col">No</th>
                                <th scope="col">Date</th>
                                <th scope="col">Name</th>
                                <th scope="col">Assign Title</th>
                                <th scope="col">Sent</th>
                                <th scope="col">Creative%</th>
                                <th scope="col">Satisfied%</th>
                                <th scope="col">Correction%</th>
                                <th scope="col">Scale</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr scope="row" v-for="(review,index) in getReviews" :key="index">
                                <td data-label="no" scope="col">{{index+1}}</td>
                                <td data-label="date" scope="col">{{review[0]}}</td>
                                <td data-label="name" scope="col">{{review[1]}}</td>
                                <td data-label="assign title" scope="col">{{review[2]}}</td>
                                <td data-label="sent" scope="col">{{review[3]}}</td>
                                <td data-label="creative%" scope="col">{{review[4]}}</td>
                                <td data-label="satisfied%" scope="col">{{review[5]}}</td>
                                <td data-label="correction%" scope="col">{{review[6]}}</td>
                                <td data-label="scale" scope="col">{{ getScale(Number(review[4]),Number(review[5]),Number(review[6])) }}</td>
                            </tr>
                        </tbody>
                      </table>

                    
                    
                </div>
            
            </center>
                
            
        </div>
    </div>
    <div class="mb-2" v-if="getReviews.length>0">
        <button class="btn btn-primary me-2" @click="exportToPDF">Export to PDF<i class="fa-solid fa-file-export ms-2"></i></button>
        <button class="btn btn-danger" @click="deleteAll">Delete All<i class="fa-solid fa-trash ms-2"></i></button>
    </div>
    <button v-if="getReviews.length>0" @click="toggleRowEdit" class="btn mb-2 text-danger ">Remove Row<i class="fa-solid ms-2" :class="rowEdit?'fa-caret-up':'fa-caret-down'"></i></button>
    <div class="row" v-if="rowEdit&&getReviews.length>0">
        <div class="col-md-4 offset-md-4">
            <div class="card card-body">
                <p class="border-bottom mb-1 pb-2" v-for="(review,index) in getReviews" :key="index">No. {{ index+1 }} | {{ review[1] }} | <i class="fa-solid fa-square-minus text-danger cursor-pointer" @click="removeRow(index)"></i></p>
            </div> 
        </div>
    </div>   

</template>

<script>
import Swal from 'sweetalert2/dist/sweetalert2.min.js';

import html2pdf from 'html2pdf.js'
import {mapGetters} from "vuex"
    export default {
        name : 'ExportPdf',
        data() {
            return {
                title : '',
                titleEdit : true,
                rowEdit : false,
            }
        },
        computed: {
           
            ...mapGetters(["getReviews"]),
        },
        mounted () {
            this.loadFromLocalStorage();
        },
        methods: {
            toggleTittleEdit(){
                this.titleEdit = !(this.titleEdit);
                this.saveToLocalStorage();
            },
            toggleRowEdit(){
                this.rowEdit = !(this.rowEdit);
            },
            saveToLocalStorage(){
                localStorage.setItem('table_title',JSON.stringify(this.title));
            },
            loadFromLocalStorage(){
                const local_table_title = localStorage.getItem('table_title');
                if(local_table_title){
                    this.titleEdit = false;
                    this.title = JSON.parse(local_table_title);
                }
            },
            exportToPDF(){
                html2pdf(document.getElementById("element-to-convert"),{
                    margin : 1,
                    filename : `${this.title}.pdf`,
                    image: { type: 'jpeg', quality: 0.98 }, // Optional: Set the image format and quality
                    html2canvas: { scale: 4 }, // Optional: Adjust the scale for rendering the HTML to canvas
                    jsPDF: { 
                    unit: 'mm', // Set the unit for the page size to millimeters
                    format: 'a4', // Set the page format to A4 size
                    orientation: 'landscape' // Set the orientation to 'portrait' or 'landscape'
                    }
                });
            },
             getScale(creative,satisfied,correction){
                let scale = ((Number(creative)+Number(satisfied))-Number(correction))/2*5/100 ;
                return scale;
            },
            deleteAll(){
                Swal.fire({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete all!'
                }).then((result) => {
                if (result.isConfirmed) {
                    this.$store.commit("deleteAll");
                    Swal.fire(
                    'Removed!',
                    'Table datas has been deleted.',
                    'success'
                    )
                }
                })
            },
            removeRow(index){
                this.$store.commit("removeRow",index);
            }
        }
    }

</script>

<style>


#html2pdf h4{
    cursor: pointer;
}

table {
    border: 1px solid #ccc;
    border-collapse: collapse;
    margin: 0;
    padding: 0;
    width: 100%;
    table-layout: fixed;
  }
  
  table caption {
    font-size: 1.5em;
    margin: .5em 0 .75em;
  }
  
  table tr {
    border: 1px solid #ddd;
    padding: .35em;
  }
  
  table th,
  table td {
    padding: .625em;
    text-align: center;
  }
  
  table th {
    font-size: .85em;
    letter-spacing: .1em;
    text-transform: uppercase;
  }
  
  @media screen and (max-width: 600px) {
    table {
      border: 0;
    }
  
    table caption {
      font-size: 1.3em;
    }
    
    table thead {
      border: none;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px;
    }
    
    table tr {
      border-bottom: 3px solid #ddd;
      display: block;
      margin-bottom: .625em;
    }
    
    table td {
      border-bottom: 1px solid #ddd;
      display: block;
      font-size: .8em;
      text-align: right;
    }
    
    table td::before {
      /*
      * aria-label has no advantage, it won't be read inside a table
      content: attr(aria-label);
      */
      content: attr(data-label);
      float: left;
      font-weight: bold;
      text-transform: uppercase;
    }
    
    table td:last-child {
      border-bottom: 0;
    }
  }
  
  
</style>